.questions {
  background-image: url("../img/background.webp");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-color: $color-default-black;

  @media screen and (max-width: 1199px) {
    background-image: url("../img/background-tablet.webp");
  }

  @media screen and (max-width: 767px) {
    background-image: url("../img/background-mobile.webp");
  }
}

.questions__wrapper {
  padding: 312px 103px 312px 88px;

  @media screen and (max-width: 1199px) {
    padding: 195px 45px;
  }

  @media screen and (max-width: 767px) {
    padding: 181px 15px 183px 15px;
  }
}

.questions__wrapper h2 {
  font-size: 35px;
  font-weight: 800;
  line-height: 1;
  color: $color-default-white;
  text-align: center;
  margin-bottom: 17px;

  @media screen and (max-width: 1199px) {
    font-size: 25px;
    margin-bottom: 19px;
  }

  @media screen and (max-width: 767px) {
    font-size: 22px;
    margin-bottom: 14px;
  }
}

.questions__wrapper p {
  font-size: 16px;
  line-height: 24px;
  color: $color-default-white;
  text-align: center;
  margin-bottom: 40px;

  @media screen and (max-width: 1199px) {
    max-width: 295px;
    margin: 0 auto;
    margin-bottom: 29px;
  }

  @media screen and (max-width: 767px) {
    font-size: 12px;
    line-height: 18px;
    margin-bottom: 26px;
    max-width: 100%;
  }
}

.questions__form-wrapper {
  margin: 0 auto;
}

.questions__form-wrapper form {
  display: flex;
  gap: 30px;
  justify-content: center;
  padding-left: 13px;
  //padding-left: 142px;

  @media (min-width: 1200px) and (max-width: 1439px) {
    padding-left: 80px;
  }

  @media screen and (max-width: 1199px) {
    display: block;
    padding-left: 0;
  }
}

.questions__form-wrapper .custom-input {
  width: 340px;

  @media screen and (max-width: 1199px) {
    width: 266px;
    margin: 0 auto;
  }

  @media screen and (max-width: 767px) {
    width: 100%;
  }
}

.questions__form-wrapper .custom-input:first-of-type {
  @media screen and (max-width: 1199px) {
    margin-bottom: 7px;
  }
}

.questions__form-wrapper input {
  width: 340px;
  padding: 24px 30px;
  font-size: 16px;
  line-height: 1;
  border: none;

  @media screen and (max-width: 1199px) {
    width: 266px;
    font-size: 14px;
    padding: 16px 15px;
  }

  @media screen and (max-width: 767px) {
    width: 100%;
  }
}

.questions__form-wrapper .btn {
  display: block;
  padding-top: 23px;
  min-width: 238px;
  height: 66px;

  @media screen and (max-width: 1199px) {
    margin: 0 auto;
    margin-top: 15px;
    padding-top: 22px;
    min-width: 266px;
    height: 59px;
  }

  @media screen and (max-width: 767px) {
    margin-top: 8px;
    padding: 17px 20px 17px 20px;
    min-width: 100%;
    height: 53px;
  }
}
